.ModalWrapper {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4px);
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 16px;
}

.Modal {
  border-radius: 4px;
  background: #29313a;
  border-radius: 8px;
  border: 1px solid #343c47;
  overflow: hidden;
}

.Top {
  padding: 16px;
}

.Title {
  font-weight: 700;
  color: #22d49f;
  letter-spacing: 1px;
  font-size: 16px;
}

.Body {
  padding: 16px;
}
