.MenuItem {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 8px;
  padding: 8px 10px;
  border-radius: 8px;
  cursor: pointer;
  /* background: #232a32;*/
  transition: 0.1s;
  position: relative;
  color: #ddd;
}
.ItemDisabled {
  cursor: default;
  color: #545c66;
}

.MenuItem.Selected {
  background: #343c47;
  color: #fff;
}
.MenuItem:not(.ItemDisabled):not(.Selected):hover {
  background: #343c4782;
}

.ItemIcon {
  width: 16px;
  height: 16px;
}
.MenuItem path {
  stroke: #ddd !important;
}

.MenuItem.ItemDisabled path {
  stroke: #545c66 !important;
}

.MenuItem.Selected path {
  stroke: #fff !important;
}

.ItemName {
  display: flex;
  align-items: center;
  font-weight: 500;
  gap: 8px;
  font-size: 13px;
  user-select: none;
}
