.Wallet {
  display: flex;
  gap: 14px;
  align-items: center;
  width: 195px;
  justify-content: flex-end;
  height: 36px;
}

.Address {
  color: #545c66;
  font-size: 14px;
  font-weight: 600;
}

.Account {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.Balance {
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  display: flex;
  gap: 2px;
}

.NetworkLogo {
  width: 40px;
  height: 40px;
}

.Right {
  position: relative;
}

.NetworkSwitcher {
  position: absolute;
  display: flex;
  right: -300px;
  top: 68px;
  transition: 0.5s;
  opacity: 0;
}

.NetworkSwitcher.Opened {
  right: -24px;
  opacity: 1;
}

.SwitcherContainer {
  padding: 14px 24px;
  border-radius: 0 0 0 24px;
  background: #181d23;
  padding-top: 0;
}

.LeftContainer {
  background: #181d23;
  width: 24px;
  height: 48px;
}

.Rounder {
  width: 24px;
  height: 48px;
  border-radius: 0 32px 0 0;
  background: #232a32;
}
