html,
body {
  overflow: hidden;
}

.App {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr;
  overflow: hidden;
  max-width: 2000px;
  margin: 0 auto;
  border: 1px solid #343c47;
}

.Container {
  display: grid;
  grid-template-columns: auto 1fr;
  overflow: hidden;
}
