.Slider {
  height: 42px;
  display: grid;
  align-items: center;
  position: relative;
}

.Thumb {
  width: 36px;
  height: 28px;
  background: #232a32;
  cursor: pointer;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: 500;
  border-radius: 12px;
  position: absolute;
  top: -12px;
  border: 1px solid #343c47;
  font-size: 13px;
  background: linear-gradient(90deg, #6046ff, #22d49f);
}

.Thumb:hover:active {
  transform: scale(0.95);
}
.Track {
  background: #1f262f;
  height: 6px;
  border-radius: 4px;
  border: 1px solid #343c47;
}

.Mark {
  width: 8px;
  position: absolute;
  left: 0;
  height: 8px;
  background: #000;
  border-radius: 8px;
}
