.Label {
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  padding-bottom: 8px;
  padding-left: 8px;
}

.InputWrapper {
  background: linear-gradient(90deg, #6046ff, #22d49f);
  animation: Animation 2s ease infinite;
  display: grid;
  border-radius: 9px;
  box-sizing: border-box;
  background-size: 200% 200%;
}

.Input {
  border: none;
  outline: none;
  padding: 10px 16px;
  border-radius: 8px;
  border: 1px solid #1f262f;
  background: #1f262f;
  color: #fff;
  font-size: 13px;
  margin: 0;
  box-sizing: border-box;
  transition: 0.1s;
  border: 1px solid #343c47;

  font-weight: 500;
}
.Input::placeholder {
  color: #343c47;
}
.Input:focus {
  padding: 9px 15px;
  margin: 1px;
}

@keyframes Animation {
  0% {
    background-position: 10% 0%;
  }
  50% {
    background-position: 91% 100%;
  }
  100% {
    background-position: 10% 0%;
  }
}
