.Network {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 8px;
  padding: 8px 10px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.1s;
  background: #232a32;
  color: #ccc;
}

.Network.Opened {
  background: #343c47;
  border-radius: 8px 8px 0 0;

  margin-bottom: 0px;
  color: #fff;
  margin-top: 4px;
  margin-bottom: 2px;
}

.Network:not(.Opened):hover {
  background: #343c4782;
}
.NetworkItems {
  display: none;
  background: #232a32;
  border-radius: 8px;
  opacity: 0;
  padding: 8px 4px;
  gap: 4px;
  margin-bottom: 4px;
  transition: 0.2s;
}
.NetworkItems.Opened {
  display: grid;
  opacity: 1;
  border-radius: 0 0 8px 8px;
}

.NetworkName {
  font-weight: 500;
  font-size: 13px;
  padding-right: 4px;
  user-select: none;
}

.Chevron {
  width: 14px;
  height: 14px;
  transition: 0.2s;
  padding: 4px;
}

.Chevron.Opened {
  transform: rotate(180deg);
}

.Chevron path {
  stroke: #fff !important;
}
.NetworkLogo {
  width: 22px;
  height: 22px;
  background: #181d23;
  border-radius: 8px;
  padding: 4px;
  box-sizing: border-box;
}
