.Container {
  padding: 24px 16px;
  background: #29313a;
  border-radius: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border: 1px solid #343c47;
  gap: 20px;
  padding-bottom: 32px;
}

.Title {
  font-weight: 700;
  color: #545c66;
  font-size: 14px;
  padding-bottom: 8px;
}
.ButtonContainer {
  display: flex;
  justify-content: flex-end;
  padding-top: 24px;
}
