.ToolScreen {
  padding: 0 16px;
  background: #232a32;
  padding-bottom: 32px;
  max-width: 700px;
  margin: 0 auto;
  overflow: scroll;
}

.Top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-items: center;
  padding: 32px 0;
  padding-bottom: 16px;
}

.ScreenTitle {
  font-size: 20px;
  font-weight: 600;
  color: #fff;
  display: flex;
  gap: 8px;
  align-items: center;
}

.ScreenTitle svg {
  width: 32px;
  height: 32px;
  background: #181d23;
  border-radius: 8px;
  padding: 6px;
  box-sizing: border-box;
}

.ScreenTitle path {
  stroke: #fff !important;
}

.ScreenDescription {
  font-weight: 600;
  color: #fff;
  font-size: 20px;
  padding: 0 0;
  background: linear-gradient(90deg, #6046ff, #22d49f);
  padding: 16px 24px;
  border-radius: 16px;
  margin-top: 32px;
  line-height: 32px;
  text-align: justify;
}

.On {
  font-weight: 700;
  font-size: 16px;
  color: #545c66;
}

.Network {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #545c66;
  font-size: 16px;
}

.Network img {
  width: 32px;
  height: 32px;
  background: #181d23;
  border-radius: 8px;
  padding: 6px;
  box-sizing: border-box;
}

@media screen and (max-width: 700px) {
  .Top {
    flex-direction: column;
    gap: 8px;
    align-items: center;
    justify-content: center;
  }
}
