@import url("https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@800&display=swap");
.TopBarWrapper {
  display: grid;
  padding-bottom: 0px;
  background: linear-gradient(90deg, #6046ff, #22d49f);
  border-bottom: 1px solid #343c47;
}
.TopBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #181d23;
  padding: 8px 16px;
}

.Logo {
  font-size: 14px;
  font-weight: 700;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.1s;
  display: flex;
  align-items: center;
  width: 430px;

  gap: 8px;
}

.Logo img {
  width: 24px;
  height: 24px;
}

.Left {
  color: #fff;
  font-family: "Gemunu Libre", sans-serif !important;
  font-size: 18px;
}

.Right {
  color: #22d49f;
}
