.ScreenTitle {
  display: flex;
  font-size: 32px;
  font-weight: 700;
  color: #fff;
  align-items: center;
  gap: 16px;
}

.Liqs {
  display: grid;
  gap: 0;
  box-shadow: #181d237e 0px 4px 12px;
}

.Token {
  padding: 10px 12px;
  border-radius: 0;
  background: #29313a;
  transition: 0.1s;
  display: flex;
  justify-content: space-between;
  border: 1px solid #343c47;
  border-bottom: none;
  align-items: center;
  gap: 16px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.OnHover {
  opacity: 0;
  transition: 0.4s;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  backdrop-filter: blur(4px);
}

.OnHover.Visible {
  transition: 0.2s;
  opacity: 1;
}

.IconButton {
  height: 20px;
  background: linear-gradient(90deg, #6046ff, #22d49f);
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 12px;
  background-size: 150% 150%;
  background-position: 25% 25%;
  transition: 0.2s;
  font-weight: 700;
  color: #fff;
  font-size: 13px;
}

.IconButton svg {
  width: 16px;
  height: 16px;
}

.IconButton:hover {
  background-position: 75% 75%;
}

.IconButton path {
  stroke: #fff !important;
}

.Token:first-child {
  border-radius: 8px 8px 0 0;
}
.Token:last-child {
  border-radius: 0 0 8px 8px;
  border-bottom: 1px solid #343c47;
}
.Token:hover {
  background: rgb(44, 53, 65);
}

.Name {
  font-weight: 500;
  color: #fff;
  font-size: 13px;
}

.Balance {
  font-weight: 500;
  color: #fff;
  font-size: 13px;
}

.Created {
  color: #545c66;
  font-size: 13px;
  font-weight: 500;
}

@media screen and (max-width: 700px) {
  .Liqs {
    gap: 8px;
  }
  .Token {
    flex-direction: column;
    border-bottom: 1px solid #343c47;
    border-radius: 8px !important;
  }
  .Header {
    display: none;
  }
}
