.Card {
  background: #29313a;
  border-radius: 8px;
  border: 1px solid #343c47;
  overflow: hidden;
  box-shadow: #181d237e 0px 4px 12px;
}

.CardTitle {
  padding: 16px 24px;
  background: #343c47;
  color: #fff;
  font-weight: 600;
  font-size: 13px;
}

.CardBody {
  padding: 24px 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}
