.ButtonWrapper {
  border-radius: 13px;
  padding: 0x;
  margin: 1px;
  background: transparent;
  transition: 0.2s;
}

.ButtonWrapper.Selected {
  background: linear-gradient(90deg, #6046ff, #22d49f);
  /*animation: Animation 1s ease infinite;*/
  background-size: 200% 200%;
  margin: 0px;
  padding: 1px;
}

@keyframes Animation {
  0% {
    background-position: 10% 0%;
  }
  50% {
    background-position: 91% 100%;
  }
  100% {
    background-position: 10% 0%;
  }
}

.Button {
  border-radius: 12px;
  background: #181d23;
  display: grid;
  place-items: center;
  transition: 0.05s;
  cursor: pointer;
}

.Selected .Button {
  /*background: #343c47;*/
}

.Button:hover {
  background: #232a32;
}

.ButtonWrapper:hover:active {
  transform: scale(0.95);
}

.Big {
  width: 38px;
  height: 38px;
}

.Big img {
  width: 26px;
  height: 26px;
  margin: 2px;
}
