.Button {
  display: inline-flex;
  color: #fff;

  justify-content: space-between;
  gap: 12px;
  align-items: center;
  padding: 8px 16px;
  background: linear-gradient(90deg, #6046ff, #22d49f);
  border-radius: 8px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  background-size: 150% 150%;
  background-position: 25% 25%;
  transition: 0.2s;
}
.Button svg,
.Button img {
  width: 16px;
  height: 16px;
}

.Button path {
  stroke: #fff !important;
}

.Button:hover {
  background-position: 75% 75%;
}

.Button:hover:active {
  transform: scale(0.95);
}
