/* width */
::-webkit-scrollbar {
  width: 4px;
  position: absolute;
  display: none;
}

/* Track */
::-webkit-scrollbar-track {
  width: 4px;
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  width: 4px;
  background: #343c47;
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #343c47;
}

